import React, { Component } from 'react';
export default class About extends Component {
	render() {
		let resumeData = this.props.resumeData;
		return (
			<section id="about">
				<div className="row">
					<div className="three columns">
						<img className="profile-pic" src="images/profilepic.jpg" alt="" />
					</div>

					<div className="nine columns main-col">
						<h2>About Me</h2>
						<p>{resumeData.aboutme}</p>

						<div className="row">
							<div className="columns contact-details">
								<h2>Contact Details</h2>
								<p className="address">
									<span style={{ color: 'white' }}>{resumeData.name}</span>
									<br />
									<span style={{ color: 'white' }}>{resumeData.address}</span>
									<br />
									<a
										href="mailto:AstridFlare@Gmail.com?Subject=I%20Want%20To%20Hire%20You!"
										target="_top"
									>
										{resumeData.email}
									</a>
									| <span style={{ color: 'white' }}>{resumeData.phone}</span>
								</p>
							</div>
						</div>
					</div>
				</div>
			</section>
		);
	}
}
